import React    from "react";
import template from "./Login.jsx";

class Login extends React.Component {
  constructor(props){
    super(props);
    this.textInput = React.createRef();
  }
  Login = e => {
    e.preventDefault();
    // check mail
    const mail = e.target.email.value;
    const reEmail = /^((\w+@[a-zA-Z_]+?\.[a-zA-Z]{2,6}))$/;
    const validMail = reEmail.test(mail);
    if (validMail) {
      e.target.email.parentElement.style.border = '2px solid green';
    } else {
      e.target.email.parentElement.style.border = '2px solid red';
    }

    //check Password
    const rePassword = /^((?=.*[\d])(?=.*[a-z])(?=.*[A-Z])|(?=.*[a-z])(?=.*[A-Z])(?=.*[^\w\d\s])|(?=.*[\d])(?=.*[A-Z])(?=.*[^\w\d\s])|(?=.*[\d])(?=.*[a-z])(?=.*[^\w\d\s])).{7,30}$/g;
    const password = e.target.password.value;
    const validPassword = password.match(rePassword);
    if (!validPassword) {
      e.target.password.parentElement.style.border = '2px solid red';
    } else {
      e.target.password.parentElement.style.border = '2px solid green';
    }
  }
  MouseDown = () => {
    this.textInput.current.type = '';
  }
  MouseUp = () => {
    this.textInput.current.type = 'password';
  }
  render() {
    return template.call(this);
  }
}

export default Login;
