import React    from "react";
import template from "./ProductInfo.jsx";
import { connect } from 'react-redux';

const mapStateToProps = (state) => { 
  return { 
    cartAmount: state.cartAmount 
  }; 
};


class ProductInfo extends React.Component {
  constructor() {
    super();
    this.state = {
      countValue: 1,
      showText: false,
      btnText: 'Read more',
  // for ImageGallery
      showIndex: false,
      showBullets: true,
      infinite: true,
      showThumbnails: true,
      showFullscreenButton: true,
      showGalleryFullscreenButton: true,
      showPlayButton: false,
      showGalleryPlayButton: true,
      showNav: false,
      isRTL: false,
      slideDuration: 450,
      slideInterval: 2000,
      slideOnThumbnailOver: true,
      thumbnailPosition: 'right',
      showVideo: {},
  // ------------
    }
  }
  textToggle = () => {
    this.setState({ showText: !this.state.showText });
    (this.state.showText) ? this.setState({ btnText: 'Read more' }) : this.setState({ btnText: 'Show less' })
  }
  Count = event => {
    let value = event.target.value.replace(/\D/g, '');
    value = (value < 1 || value > 20) ? 1 : value;
    this.setState({countValue: value});
    localStorage.setItem("quantity", value);
  }
  Minus = () => {
    const curValue = this.state.countValue;
    let value = parseInt(curValue)-1;
    value = value < 1 ? 20 : value;
    this.setState({countValue: value});
    localStorage.setItem("quantity", value);
  }
  Plus = () => {
    const curValue = this.state.countValue;
    let value = parseInt(curValue)+1;
    value = value > 20 ? 1 : value;
    this.setState({countValue: value});
    localStorage.setItem("quantity", value);
  }
  AddToCart() {
    const card =  JSON.parse(localStorage.getItem('card'));
    const PRODUCTS =  JSON.parse(localStorage.getItem('products'));
    const quantity = JSON.parse(localStorage.getItem('quantity'));
    if(PRODUCTS) {
      const index = PRODUCTS.findIndex((obj => obj.id === card.id));
      if (index !== -1 ) { // the same product
        PRODUCTS[index].amount += quantity;
        localStorage.setItem('products', JSON.stringify(PRODUCTS) );
      } else { // new product
        card.amount = quantity;
        PRODUCTS.push(card);
        localStorage.setItem('products', JSON.stringify(PRODUCTS) );
      }
    } else {
      let products = [];
      products.push(card);
      localStorage.setItem('products', JSON.stringify(products) );
    }
    this.props.dispatch({ type: "CART" });
  }
  componentDidMount() {
    const quantity = localStorage.quantity;
    this.setState({countValue: quantity});
    this.props.dispatch({ type: "CART" });
  }
  render() {
    return template.call(this);
  }
}

export default connect(mapStateToProps)(ProductInfo);

