class Category extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      ItemsRequest: {},
      loaderActive: false,
      showFilter: false,
      btnText: 'Hide Filter',
    };
  }
  saveCard(card) {
    localStorage.setItem("quantity", 1);
    card.amount = 1;
    localStorage.setItem('card', JSON.stringify(card.card) );
  }
  loaderActivate() {
    this.setState({ loaderActive: true });
  }
  loaderDisable() {
    this.setState({ loaderActive: false });
  }
  showFilter() {
    this.setState({ showFilter: !this.state.showFilter });
    (!this.state.showFilter) ? this.setState({ btnText: 'Show Filter' }) : this.setState({ btnText: 'Hide Filter' })
  }
  async LoadItems(e) {
    this.loaderActivate();
    try {
      const result = await axios.get('./data/popular-items.json');
      if(result) {
        this.loaderDisable();  
        this.setState({ ItemsRequest: result.data.categoryCards });
      }
    } catch(error) {
      this.loaderDisable();  
      alert('There is a problem with your request');
    }
  }
  componentDidMount() {
    this.props.dispatch({ type: "print-off" });
  }
  render() {
    if(this.state.ItemsRequest && this.state.ItemsRequest.length) {
      this.cardsRequest = this.state.ItemsRequest.map(card => 
        <div className="card" key={card.id} >
          <Card style={{ height: 370, width: 270, borderRadius: '6px',  margin: '0 auto'}}>
            <CardActionArea>
              <CardMedia image={card.img} title="logo_small" style={{height: 260}}/>
            </CardActionArea>
            <Link to='/ProductDetailed' onClick={() => this.saveCard({card}) }>
              <CardContent style={{ height: 80}}>
                <Typography variant="body2" component="p" style={{ margin: card.contentMargin ? card.contentMargin : '0 0 10px 0', color:"rgb(44, 62, 80)", fontWeight: 'bold', fontFamily: 'Lato, sans-serif' }}>
                  {card.content}<br/>
                  {card.content2}
                </Typography>
                <Typography variant="body2" component="p" style={{ color: 'rgb(189, 195, 199)', fontWeight: 'bold', fontFamily: 'Lato, sans-serif'}}>
                  $ {card.price}
                </Typography>
              </CardContent>
            </Link>
          </Card>
        </div>
      )
    }
    if(categoryCardsArr && categoryCardsArr.length) {
      this.categoryCards = categoryCardsArr.map(card => 
        <div className="card" key={card.id} >
          <Card style={{ height: 370, width: 270, borderRadius: '6px',  margin: '0 auto'}}>
            <CardActionArea>
            <CardMedia image={card.img} title="logo_small" style={{height: 260}}/>
            </CardActionArea>
            <Link to='/ProductDetailed' onClick={() => this.saveCard({card}) }>
              <CardContent style={{ height: 80}}>
                  <Typography variant="body2" component="p" style={{ margin: card.contentMargin ? card.contentMargin : '0 0 10px 0', color:"rgb(44, 62, 80)", fontWeight: 'bold', fontFamily: 'Lato, sans-serif' }}>
                    {card.content}<br/>
                    {card.content2}
                  </Typography>
                <Typography variant="body2" component="p" style={{ color: 'rgb(189, 195, 199)', fontWeight: 'bold', fontFamily: 'Lato, sans-serif'}}>
                  $ {card.price}
                </Typography>
              </CardContent>
            </Link>
          </Card>
        </div>)
    }

    return template.call(this);
  }
}

export default connect(mapStateToProps)(Category);

