const mapStateToProps = (state) => {
  return {
    cartAmount: state.cartAmount,
    wishes: state.wishes
  };
};

class PopularItems extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      ItemsRequest: {},
      loaderActive: false,
    };
  }
  loaderActivate() {
    this.setState({ loaderActive: true });
  }
  loaderDisable() {
    this.setState({ loaderActive: false });
  }
  countWishes() {
    if(localStorage.wishes) {
      localStorage.wishes = +localStorage.wishes + 1;
      localStorage.wishes = localStorage.wishes > 20  ? 0 : localStorage.wishes;
    } else {
      localStorage.wishes = 1;
    }
    this.props.dispatch({ type: "GET_wishes" });
  }
  saveCard(card) {
    localStorage.setItem("quantity", 1);
    card.amount = 1;
    localStorage.setItem('card', JSON.stringify(card) );
  }
  addToCart(card) {
    const PRODUCTS =  JSON.parse(localStorage.getItem('products'));
    if(PRODUCTS) {
      const index = PRODUCTS.findIndex((obj => obj.id === card.id));
      if (index !== -1 ) { // the same product
        PRODUCTS[index].amount += 1;
        localStorage.setItem('products', JSON.stringify(PRODUCTS) );
      } else { // new product
        card.amount = 1;
        PRODUCTS.push(card);
        localStorage.setItem('products', JSON.stringify(PRODUCTS) );
      }
    } else {
      let products = [];
      card.amount = 1;
      products.push(card);
      localStorage.setItem('products', JSON.stringify(products) );
    }
    this.props.dispatch({ type: "CART" });
  }
  async LoadItems(e) {
    this.loaderActivate();
    try {
      const result = await axios.get('./data/popular-items.json');
      if(result) {
        this.loaderDisable();  
        this.setState({ ItemsRequest: result.data.cards });
      }
    } catch(error) {
      this.loaderDisable();  
      alert('There is a problem with your request');
    }
  }
  render() {
    // request
    if(this.state.ItemsRequest && this.state.ItemsRequest.length) {
      this.cardsRequest = this.state.ItemsRequest.map(card => 
        <div className="card" key={card.id} >
          <Card style={{ height: 370, width: 270, borderRadius: '6px'}}>
            <CardMedia image={card.img} title="logo_small" style={{height: 260}}/>
            <CardContent style={{ height: 80}}>
              <Typography variant="body2" component="p" style={{ margin: card.contentMargin ? card.contentMargin : '0 0 10px 0', color:"#45413e" }}>
                {card.content} <br/>
                {card.content2}
              </Typography>
              <Typography variant="body2" component="p" style={{ color: '#011847'}}>
                $ {card.price}
              </Typography>
            </CardContent>
          </Card>
          <div className="overlay green">
            <IconButton className="card-btn-circle" style={{ backgroundColor: "#fff", margin: "0 10px" }}>
              <img src="./icons/plus.png" alt="plus"/>
            </IconButton>
            <IconButton className="card-btn-circle" style={{ backgroundColor: "#fff", margin: "0 10px" }}>
              <img src="./icons/heart_card.png" alt="heart"/>
            </IconButton>
          </div>
        </div>
        )} 
    

    // static Cards
    if(staticCardsArr.length) {
      this.staticCards = staticCardsArr.map(card => {
        if(card.style === 'green') { return (
          <div className="card" key={card.id} >
            <Card style={{ height: 370, width: 270, borderRadius: '6px'}}>
              <CardMedia image={card.img} title="logo_small" style={{height: 260}}/>
              <CardContent style={{ height: 80}}>
                <Typography variant="body2" component="p" style={{ margin: card.contentMargin ? card.contentMargin : '0 0 10px 0', color:"#45413e" }}>
                  {card.content} <br/>
                  {card.content2}
                </Typography>
                <Typography variant="body2" component="p" style={{ color: '#011847'}}>
                  $ {card.price}
                </Typography>
              </CardContent>
            </Card>
            <div className="overlay green">
              <IconButton className="card-btn-circle" style={{ backgroundColor: "#fff", margin: "0 10px" }} onClick={() => this.addToCart(card)}>
                <img src="./icons/plus.png" alt="plus"/>
              </IconButton>
              <IconButton className="card-btn-circle" style={{ backgroundColor: "#fff", margin: "0 10px" }} onClick={() => this.countWishes()}>
                <img src="./icons/heart_card.png" alt="heart"/>
              </IconButton>
            </div>
        </div> 
        )} else if(card.style === 'buttons') { return (
          <div className="card" key={card.id}>
            <Card style={{ height: 370, width: 270, borderRadius: '6px'}}>
              <Link to='/ProductDetailed' onClick={() => this.saveCard(card)}>
                <CardMedia image={card.img} title="logo_small" style={{height: 260}}/>
              </Link>
              <CardContent style={{ height: 80}}>
                <Typography variant="body2" component="p" style={{ margin: card.contentMargin ? card.contentMargin : '0 0 10px 0', color:"#45413e", padding:'0 35px'}}>
                  <Link to='/ProductDetailed' onClick={() => this.saveCard(card)}>
                    {card.content}  <br/>
                    {card.content2}
                  </Link>
                </Typography>
                <div className="card-btn-wr">
                  <p className="card-btn__price">
                    <Link to='/ProductDetailed' onClick={() => this.saveCard(card)}>$ {card.price}</Link>
                  </p>
                  <button className="card-btn-green" onClick={() => this.addToCart(card)}>Buy now</button>
                </div>
              </CardContent>
            </Card>
        </div> 
        )} else { return (
          <div className="card" key={card.id}>
            <Card style={{ height: 370, width: 270, borderRadius: '6px' }}>
              <CardMedia image={card.img} title="logo_small" style={{height: 370}}/>
            </Card>
            <div className="overlay violet">
              <CardActionArea>
                <div className="overlay__content">
                  <p> {card.content}</p>
                  <div className="overlay__date">
                    <img className="overlay__icon" src={card.icon} alt="icon"/> 
                    <p>{card.date}</p>
                  </div>
                </div>
              </CardActionArea>
            </div>
          </div>
        )}
          
      })
    }


    return template.call(this);
  }
}

export default connect(mapStateToProps)(PopularItems);
