import React    from "react";
import template from "./Header.jsx";
import { connect } from 'react-redux';


const mapStateToProps = (state) => {
  return {
    cartAmount: state.cartAmount,
    wishes: state.wishes,
    printActive: state.printActive,
  };
};


class Header extends React.Component {
    constructor() {
    super()
    this.state = {
      isActive: false,
      showLogin: false,
    }
  }
  toggleActive() {
    this.setState({ isActive: !this.state.isActive })
  }
  clearWishes() {
    localStorage.setItem('wishes', 0);
    this.props.dispatch({ type: "GET_wishes" });
  }
  LoginOpen =() => this.setState(state => ({showLogin: !state.showLogin}));
  componentDidMount() {
    this.props.dispatch({ type: "GET_wishes" });
    this.props.dispatch({ type: "CART" });
  }
  render() {
    return template.call(this);
  }
}

export default connect(mapStateToProps)(Header);


