const mapStateToProps = (state) => { 
  return { 
    cartAmount: state.cartAmount,
    printActive: state.printActive,
  }; 
};


class ProductDetailed extends React.Component {
  constructor() {
    super();
    this.anchor = React.createRef();
  }
  saveCard(card) {
    localStorage.setItem("quantity", 1);
    card.amount = 1;
    localStorage.setItem('card', JSON.stringify(card) );
  }
  ScrollTop = () => {
    this.anchor.current.scrollIntoView({behavior: 'smooth'});
  }
  componentDidMount() {
    this.props.dispatch({ type: "print-off" });
  }
  render() {
    if(popularItemsArr.length) {
      this.popularItems_productPage = popularItemsArr.map(card => 
        <div className="card" key={card.id} >
          <Card style={{ height: 370, width: 270, borderRadius: '6px',  margin: '0 auto'}}>
            <CardActionArea>
              <CardMedia image={card.img} title="logo_small" style={{height: 260}}/>
            </CardActionArea>
            <Link to='/ProductDetailed' onClick={() => this.saveCard(card) }>
              <CardContent style={{ height: 80}}>
                <Typography variant="body2" component="p" style={{ margin: '10px 0 20px 0', color:"#45413e" }}>
                  {card.content}
                </Typography>
                <Typography variant="body2" component="p" style={{ color: '#011847'}}>
                  $ {card.price}
                </Typography>
              </CardContent>
            </Link>
          </Card>
        </div>)
    }

    return template.call(this);
  }
}

export default connect(mapStateToProps)(ProductDetailed);

