import React    from "react";
import template from "./Cookies.jsx";

class Cookies extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      cookiesActive: !localStorage.cookiesActive
    }
  }
  componentDidMount() {
    document.addEventListener("keydown", this.escPressed, false); // ESC - btn
    const currentComponent = this;
    if(localStorage.cookiesActive === 'false') {
      currentComponent.SHOW(false);
    }
    if(!localStorage.cookiesActive) {
      setTimeout(function() { 
        currentComponent.SHOW(true);
      }, 1000);
    }
  }
  componentWillUnmount() {
    document.removeEventListener("keydown", this.escPressed, false); // ESC - btn
  }
  SHOW = (bool) => {
    this.setState({ cookiesActive: bool });
  }
  CookieClose = () => {
    this.SHOW(!this.state.cookiesActive); 
  }
  CookieAccept = () => {
    this.SHOW(false);
    localStorage.cookiesActive = false;
  }
  escPressed = (event) => { // ESC - btn
    if(event.keyCode === 27) {
      this.SHOW(!this.state.cookiesActive);
    }
  }
  render() {
    return template.call(this);
  }
}

export default Cookies;
