import React    from "react";
import template from "./Cart.jsx";
import { connect } from 'react-redux';

const mapStateToProps = (state) => { 
  return { 
    cartAmount: state.cartAmount,
    printActive: state.printActive, 
  };
 };

class Cart extends React.Component {
  constructor() {
    super();
    this.state = {
      products: [],
      subTotalPrice: 0,
      Total: 0,
    }
  }
  RadioValue = e => {
    const totalPrice = (+this.state.subTotalPrice + (+e.target.value));
    this.setState({Total: totalPrice.toFixed(2)});
  }
  Count = (e, id) => {
    let value = +e.target.value.replace(/\D/g, '');
    value = (value < 1 || value > 20) ? 1 : value;
    const PRODUCTS = this.state.products;
    const index = PRODUCTS.findIndex((obj => obj.id === id));
    PRODUCTS[index].amount = value;
    localStorage.setItem('products', JSON.stringify(PRODUCTS) );
    this.setState({products: PRODUCTS});
    this.countSubtotalPrice(PRODUCTS);
    this.props.dispatch({ type: "CART" });
  }
  Minus = (product) => {
    const PRODUCTS = this.state.products;
    const index = PRODUCTS.findIndex((obj => obj.id === product.id));
    PRODUCTS[index].amount -= 1;
    PRODUCTS[index].amount = PRODUCTS[index].amount < 1 ? 1 : PRODUCTS[index].amount;
    localStorage.setItem('products', JSON.stringify(PRODUCTS) );
    this.setState({products: PRODUCTS});
    this.countSubtotalPrice(PRODUCTS);
    this.props.dispatch({ type: "CART" });
  }
  Plus = (product) => {
    const PRODUCTS = this.state.products;
    const index = PRODUCTS.findIndex((obj => obj.id === product.id));
    PRODUCTS[index].amount += 1;
    PRODUCTS[index].amount = PRODUCTS[index].amount > 20 ? 20 : PRODUCTS[index].amount;
    localStorage.setItem('products', JSON.stringify(PRODUCTS) );
    this.setState({products: PRODUCTS});
    this.countSubtotalPrice(PRODUCTS);
    this.props.dispatch({ type: "CART" });
  }
  deleteProduct(card) {
    const rest = this.state.products.filter(obj => obj.id !== card.id);
    this.setState({products: rest});
    this.countSubtotalPrice(rest);
    localStorage.setItem('products', JSON.stringify(rest) );
    this.props.dispatch({ type: "CART" });
  }
  countSubtotalPrice(PRODUCTS) {
    if(PRODUCTS) {
      const result = PRODUCTS.reduce((sum, current) => {
        return sum + current.price * current.amount;
      }, 0);
      this.setState({subTotalPrice: result.toFixed(2)});
      this.setState({Total: result.toFixed(2)});
    }
  }
  componentDidMount() {
    const PRODUCTS =  JSON.parse(localStorage.getItem('products'));
    this.countSubtotalPrice(PRODUCTS);
    this.setState({products: PRODUCTS});
    this.props.dispatch({ type: "CART" });
    this.props.dispatch({ type: "print-off" });
  }
  render() {

    if(this.state.products && this.state.products.length) {
      this.products = this.state.products.map(product => 
        <div className="cart__card"  key={product.id}>
          <div className="card-block">
            <div className="card-image">
              <img src={product.img} alt="product"/>
            </div>
            <div className="card-info">
              <h5 className="card-title">{product.content} {product.content2}</h5>
              <p className="card-price">${product.price}</p>
            </div>
          </div>
          <div className="card-block">
            <div className="counter-wr">
              <button className="counter-btn" onClick={() => this.Minus(product)}>-</button>
              <input type="text" className="counter-input" value={product.amount} onChange={(e) => this.Count(e, product.id)}/>
              <button className="counter-btn" onClick={() => this.Plus(product)}>+</button>
            </div>
            <div className="total-price-wr">
              <p className="total-price">${(product.price * product.amount).toFixed(2)}</p>
            </div>
            <div className="delete-wr">
              <button className="delete" onClick={() => this.deleteProduct(product)}></button>
            </div>
          </div>
        </div>
    )} else this.products = (
      <div className="empty-wr">
        <img className="empty__icon" src="./icons/empty-cart.png" alt="empty-cart"/> 
        <p className="empty__message">Cart is empty ...</p>
      </div>
    )
    
    return template.call(this);
  }
}


export default connect(mapStateToProps)(Cart);



