import "./Cookies.scss";
import React from "react";

function template() {
  return (
    <div className={`cookies-wr ${this.state.cookiesActive ? 'active' : '' }`}>
      <div className="cookies">
        <div className="cookies__content">
          <h3 className="cookies__title">This website uses cookies</h3>
          <p>OSF uses its own and third-party cookies for statistical purposes, to know your preferences, for website performance <br/>
          and interaction with social media offering publicity tailored to your interests. If you continue browsing, we consider that you accept its use.<br/> 
          You can expand this information consulting our 
          <a className="policy-link" href="https://www.timeinc.net/subs/privacy/cookie.html"> Cookies Policy Page.</a>
          </p>
          <div className="accept-btn">
            <button className="btn-green" onClick={()=> this.CookieAccept()}>Accept</button> 
          </div>
        </div>
        <span className="cookies__close" onClick={()=> this.CookieClose()}></span>
      </div>
      <div className={`cookies__overlay ${this.state.cookiesActive ? 'active' : '' }`} onClick={()=> this.CookieClose()}></div>
    </div>
  );
};

export default template;
